import React from 'react';
import sharing from "./assets/sharing.jpg";
import { makeStyles, Typography, IconButton, withStyles, ButtonBase } from "@material-ui/core";
import { Link } from "react-router-dom";
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import carbonara from "./assets/carbonara.png"
import burrito from "./assets/burrito.png"
import FoodButton from './FoodButton';

const mainMenu = makeStyles(theme => ({
    responsive: {
        width: "100%",
        height: "auto",
        marginTop: "10px",
    },
    greeting:{
        paddingTop: "7%",
        width: "77%",
        float: "left",
        paddingLeft: "3%",
        color: theme.palette.primary.main
    },
    login:{
        paddingTop: "2%",
        width: "20%",
        float: "right",
        color: theme.palette.primary.main
    },
    iconButton:{
        padding: 24,
    },
    largeIcon:{
        fontSize:"large",
        color: theme.palette.accent.main
    },
    buttonContainer:{
        paddingTop: "5%",
        height: "20%",
    },
    suggestionContainer:{
        paddingLeft: "3%",
        paddingTop: "4%",
        paddingBot: "1%",
        height: "25%",
        color: theme.palette.primary.main,
    }
}));


const suggestionBox = makeStyles(theme => ({
    itemBox:{
        paddingTop: "20px",
        paddingRight: "20px",
        height: "100px"
    },
    imageContainer: {
        alignItems: "center",
        verticalAlign: "middle",
        float: "left",
        width: "30%"
    },
    image: {
        width: "110px"
    },
    infoContainer: {
        paddingTop: "20px",
        width: "65%",
        float: "right",
        verticalAlign: "middle",
        //backgroundColor: "grey"
    },
    description: {
        textAlign: "justify"
    }
}));



export default function Inici(){
    const classes = mainMenu();
    const suggestions = suggestionBox();
    
    return(
        <div style={{paddingBottom:'60px'}}>
            <div>
                <div class = {classes.greeting}> 
                    <Typography align="left" variant="h3">
                        Hola Maria,<br />
                        què vols menjar avui?
                    </Typography>
                </div>
                <div class = {classes.login}> 
                    <IconButton className={classes.iconButton} aria-label="account">
                        <AccountCircleIcon fontSize="large" backgroundColor="inherit"/>
                    </IconButton>
                </div>
            </div>
            
            <div align="center">
            <img src={sharing} class={classes.responsive}></img>
            </div>
            
            <FoodButton component={Link} to="/fhc6/carta" text="Veure Carta"/>
            <FoodButton component={Link} to="/fhc6/carta" text="Repetir Comanda"/>
            <div class = {classes.suggestionContainer}>
                <Typography align="left" variant="h2" color="inherit">
                    Suggerències
                </Typography>
                <div className={suggestions.itemBox}>
                    <div className={suggestions.imageContainer}>
                        <img className={suggestions.image} src={carbonara} alt="Carbonara"></img>
                    </div>
                    <div className={suggestions.infoContainer}>
                        <Typography variant="h3" align="left">
                        Carbonara
                        </Typography>
                        <Typography className={suggestions.description}>
                        La més demanada pels clients
                        </Typography>
                    </div>
                </div>
                <div className={suggestions.itemBox}>
                    <div className={suggestions.imageContainer}>
                        <img className={suggestions.image} src={burrito} alt="Burrito"></img>
                    </div>
                    <div className={suggestions.infoContainer}>
                        <Typography variant="h3" align="left">
                        Burrito Mixto
                        </Typography>
                        <Typography className={suggestions.description}>
                        Barreja de carn i verds!
                        </Typography>
                    </div>
                </div>
            </div>

        </div>

    );
}